.Bar {
  background: #00b09b; /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #00b09b, #96c93d); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #00b09b, #96c93d); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  flex-basis: 2px;
  flex-grow: 1;
  flex-shrink: 0;
}